import React from "react";
import styled from "styled-components";
import Image from "gatsby-image";

const BuildingLink = styled.a`
  margin-bottom: 10px;
  flex-basis: 32%;
  & > p {
    text-align: center;
  }
`;

const BuildingPreview = ({ building: { image, slug, name } }) => (
  <BuildingLink href={`https://www.wework.cn/building/${slug}`}>
    <Image fluid={image.sharp.fluid} alt={name} />
    <p>{name}</p>
  </BuildingLink>
);

export default BuildingPreview;
